//Responsive design
@mixin mobile {
    @media (min-width: $mobile) and (max-width: $tablet) {
        @content;
    }
}
@mixin tablet {
    @media (min-width: $tablet) and (max-width: $desktop){
        @content;
    }
}
@mixin desktop {
    @media (min-width: $desktop) {
        @content;
    }
}

//Buttons
@mixin primaryBtn {
    text-align: center;
    background-color: $primary;
    color: $white;
    border: 2px solid $primary;
    border-radius: 0.5rem;
    padding: 1.5rem 2.9rem;
    font-size: 1.8rem;
    font-weight: $bold;
    cursor: pointer;
    box-shadow: 0px 5px 10px 0px rgba($color: #b049c3, $alpha: 0.35);//rgba(219, 163, 52,0.35);
}
@mixin secondaryBtn {
    text-align: center;
    background-color: $white;
    color: $primary;
    border: 2px solid $primary;
    border-radius: 0.5rem;
    padding: 1.5rem 2.9rem;
    font-size: 1.8rem;
    font-weight: $bold;
    cursor: pointer;
}
@mixin tertiaryBtn {
    text-align: center;
    text-decoration: underline;
    font-weight: $bold;
    font-size: 1.8rem;
    cursor: pointer;
    color: $primary;
}

@mixin cards {
    box-shadow: 0px 2px 10px 0px rgba(0,0,0,0.075);
    background-color: $white;
    border: 2px solid $lightGray;
    border-radius: 1rem;
    padding: calc($spacing / 2);
}
