.authenticate{
    background-color: $white;
    border-radius: 1rem;
    padding: $spacing calc($spacing / 2);
    height: max-content;
    box-shadow: $boxShadow;

    .authenticate_title{
        text-align: center;
        margin-bottom: $spacing;
    }
}
.authenticate_block{
    border-bottom: 1px solid $gray;
    margin-bottom: $spacing;
    &:last-of-type{
        margin-bottom: 0;
        border-bottom: none;
    }

    .authenticate_block_info{
        margin: calc($spacing / 2) 0;
        .perfil-code{
            color: $primary;
            text-decoration: underline;
        }
    }

    .authenticate_block_list{
        margin-bottom: calc($spacing / 2);
        margin-left: calc($spacing / 2);
        .authenticate_block_li{
            margin-bottom: calc($spacing / 2);
            .validar-cert-google{
                color: $primary;
                text-decoration: underline;            }
        }
    }
}
