.main{
    margin: calc($spacing * 3) auto $spacing auto;

    .title-main{
        text-align: center;
        margin: $spacing;
    }

    .main-grid{
        display: flex;
        flex-direction: column-reverse;

        @include tablet{
            display: grid;
            grid-template-columns: 2fr 1fr;
            column-gap: $spacing;
        }

        @include desktop{
            display: grid;
            grid-template-columns: 2fr 1fr;
            column-gap: $spacing;
        }

        .authenticate{
            margin-bottom: $spacing;

            @include tablet{
                margin: 0;
            }

            @include desktop{
                margin: 0;
            }
        }
    }

}

.certificates{
    background-color: $white;
    border-radius: 1rem;
    padding: $spacing 0;
    box-shadow: $boxShadow;

    .cert-title{
        text-align: center;
        margin-bottom: $spacing;
    }
    .certificate{
        padding-bottom: 2rem;
        margin-bottom: 1rem;
        border-bottom: 1px solid $gray;
        &:last-of-type{
            border-bottom: none;
            padding-bottom: 0;
            margin-bottom: 0;
        }
        .certificate__img{
            &:hover{
                cursor: pointer;
            }
        }
    }
}
.overlay{
    position: fixed;
    background-color: rgba($color: #000000, $alpha: .5);
    width: 100%;
    height: 100%;
    top: 0;
    display: grid;
    place-items: center;
    .img-overlay{
        max-width: 96rem;
        width: 100%;
    }
    .closeBtn{
        cursor: pointer;
        background-color: $primary;
        color: $white;
        padding: 1rem 1.5rem;
        border-radius: 50%;
        margin-top: 1.5rem;
        transition: all 150ms ease-out;
        &:hover{
            background-color: $gray;
            color: $white;
        }
    }
}