.nav-container{
    box-shadow: $boxShadow;
    position: fixed;
    z-index: 200;
    width: 100%;
    top: 0;    
    background: rgba(255, 255, 255, 0.75);
    backdrop-filter: blur(5px);
}
.nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.5rem 0;

    .logo h1{
        font-weight: $medium;
        font-size: calc($spacing / 1.75);
        color: $black;
        .logo_bold{
            font-weight: $bold;
        }
    }
    .hamburguer{
        display: none;
        @include mobile {
            display: block;
        }
    }
    #sidebar .nav-btns_tel{
        position: absolute;
        z-index: 100;
        top: 0;
        right: 0;
        background: rgba(255, 255, 255, 0.75);
        backdrop-filter: blur(5px);
        box-shadow: $boxShadow;
        height: 100vh;
        width: 15rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        .nav_btns {
            display: flex !important;
            flex-direction: column;
            row-gap: 4rem;
            .btn-tel{
                font-weight: $bold;
                font-size: 1.8rem;
            }
        }
    }
    .nav_li{
        @include mobile{
            display: none;
        }
        .nav_btns{
            display: flex;
            column-gap: calc($spacing / 2.5);
            align-items: center;
            @include mobile {
                display: none;
            }
            .nav_btn{
                list-style: none;
                &:first-of-type,
                &:nth-of-type(2),
                &:nth-of-type(3){
                    @include tertiaryBtn;
                    a{
                        color: $primary;
                    }
                }
                &:nth-of-type(4){
                    @include primaryBtn;
                    a{
                        color: $white;
                    }
                }
            }
        }
    }
}

.no-display{
    display: none;
}
.active{
    display: block;
}
.absolute{
    position: absolute;
    z-index: 200;
    transform: translateX(41rem);
}