.projects-container{
    padding-top: 10rem;
    h2{
        margin: $spacing 0;
    }
}
.projects{
    margin-bottom: $spacing;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2.5rem;
    @include mobile {
        grid-template-columns: 1fr;
    }
    @include tablet{
        grid-template-columns: repeat(2, 1fr);
    }
    .project_card{
        box-shadow: $boxShadow;
        background-color: $white;
        //border: 2px solid $lightGray;
        border-radius: 1rem;
        padding: calc($spacing / 3);
        .project_info{
            .project_name{
                margin: calc($spacing / 2) 0;
            }
            .project_technologies{
                margin: calc($spacing / 2) 0;
            }
        }
        .project_img{
            width: 100%;
            margin: 0 auto;
        }
        .project_buttons{
            display: flex;
            justify-content: space-evenly;
            align-items: center;
            .repo_link{
                @include secondaryBtn
            }
            .project_link{
                @include primaryBtn;
            }
        }
    }
}
