.hero{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    justify-items: center;
    padding-top: calc($spacing * 2);
    @include mobile {
        display: block;
    }
    .hero_text{
        @include mobile{
            margin: $spacing auto;
        }    
        
        .hero_info{
            color: $black;
            font-size: 5rem;
            line-height: 7rem;
            @include mobile{
                font-size: 4.5rem;
                line-height: 5rem;
            }
            &:last-of-type{
                background-color: $primary;
                display: inline-block;
                padding: 0 2rem;
                border-radius: 1rem;
            }
        }
    }
    .hero_img{
        background-color: #d584e4;
    }
}