html{
    font-size: 62.5%;
    box-sizing: border-box;
}
*{
    margin: 0;
    padding: 0;
    font-family: $font;
}
*, *:before, *:after{
    box-sizing: inherit;
}
body{
    background-color: $gray;
    &.fijar-body{
        overflow: hidden;
    }
}
h1,h2,h3,h4{
    margin: 0;
}
img,
.fab{
    max-width: 100%;
}
h1{
    font-size: 4rem;
}
h2{
    font-size: 3.6rem;
    color: $primary2;
}
h3{
    font-size: 3rem;
    color: $primary2;
}
h4{
    font-size: 2.6rem;
}
p{
    font-size: 1.8rem;
    color: $fontColor;
    font-weight: $thin

}
li{
    font-size: 1.8rem;
    color: $fontColor;
    font-weight: $thin;

}
a{
    text-decoration: none;
    font-size: 1.8rem;
}
