.technologies{
    background-color: $white;
    box-shadow: $boxShadow;
    padding: $spacing calc($spacing / 3);
    margin: calc($spacing * 2) auto;
    border-radius: 1rem;
}

.technologies_icons{
    margin-top: $spacing;
    font-size: calc($spacing * 2.5);
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    row-gap: calc($spacing * 2);
    justify-items: center;
    @include tablet{
        grid-template-columns: repeat(3, 1fr);    
    }
    @include mobile{
        grid-template-columns: 1fr;
    }
    .technologies_icon{
        p{
            font-weight: $light;
            text-align: center;
        }
    } 
}
.fa-html5{
    color: $html;
}
.fa-css3-alt{
    color: $css;
}
.fa-js-square{
    color: $js;
}
.fa-sass{
    color: $sass;
}
.fa-git-alt{
    color: $git;
}
.fa-gulp{
    color: $gulp;
}